<template>
  <b-modal
    ref="confirmation-modal"
    id="confirmation-modal"
    @hidden="onCloseModal"
    v-model="showModel"
  >
    <!-- modal header -->
    <template #modal-header>
      <h3 class="modal-title">
        {{ confirmData.offerInfo.property_name }}
      </h3>

      <b-button size="sm" @click="onCloseModal" class="close-button">
        <svg-icon name="close-black" />
      </b-button>
    </template>
    <!-- modal body -->
    <template #default>
      <b-row class="m-0">
        <transition name="slide">
          <b-alert
            show
            v-if="serverErrors && serverErrors.Data"
            variant="danger"
          >
            <p style="text-align: center">{{ serverErrors.Data['message'] }}</p>
          </b-alert>
        </transition>
        <b-col md="12" class="offer-info">
          <div class="offer-info-title">
            {{ $t('common.yourOffer') }} {{ $t('common.price') }}
            {{ longTerm ? '/' + $t('common.month') : '/' + $t('common.night') }}
          </div>
          <div class="offer-info-value">
            {{ confirmData.form.budget }}
            {{ $t('common.egp') }}
            {{ longTerm ? '/' + $t('common.month') : '/' + $t('common.night') }}
          </div>
        </b-col>
        <b-col md="12" class="offer-info">
          <div class="offer-info-title">
            {{ $t('common.offerDuration') }}
          </div>
          <div class="offer-info-value">
            {{ confirmData.form.duration }}
            {{ longTerm ? $t('common.month') : $t('common.night') }}
          </div>
        </b-col>
        <b-col md="12" class="offer-info" v-if="shortTerm">
          <div class="offer-info-title">
            {{ $t('common.dateRange') }}
          </div>
          <div class="offer-info-value">
            {{ confirmData.form.move_in_date | formatDate }} :
            {{ confirmData.form.move_out_date | formatDate }}
          </div>
        </b-col>
        <b-col md="12" class="offer-info">
          <div class="offer-info-title">
            {{ $t('common.dateRangeFlexibility') }}
          </div>
          <div class="offer-info-value">
            {{
              confirmData.form.is_date_flexible
                ? $t('common.yes')
                : $t('common.no')
            }}
          </div>
        </b-col>
        <b-col md="12" class="offer-info">
          <div class="offer-info-title">
            {{ $t('common.guests') }}
          </div>
          <div class="offer-info-value">
            {{ confirmData.form.num_adult }} {{ $t('common.adult') }} &
            {{ confirmData.form.num_children }}
            {{ $t('common.children') }}
          </div>
        </b-col>
        <b-col md="12" class="offer-info offer-info-total">
          <div class="offer-info-title">
            {{ $t('common.total_amount') }} : ({{ confirmData.form.duration }}
            {{ longTerm ? $t('common.month') : $t('common.night') }}
            * {{ confirmData.form.budget }} {{ $t('common.egp') }})
          </div>
          <div class="offer-info-value">
            {{ confirmData.totalOffer }}
            {{ $t('common.egp') }}
          </div>
        </b-col>
      </b-row>
    </template>

    <!-- modal footer -->
    <template #modal-footer>
      <div class="modal-actions">
        <b-button
          :disabled="submitingLoading"
          type="submit"
          class="sendoffer btn btn-primary"
          @click="submit"
        >
          {{ $t('common.sendOffer') }}
        </b-button>
      </div>
    </template>
  </b-modal>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')
const { mapState: mapUserState } = createNamespacedHelpers('UserModule')

export default {
  name: 'PropertyBidDetails',
  data() {
    return {
      notes: '',
      submitingLoading: false,
      showModel: false,
    }
  },
  mounted() {
    this.$eventBus.$on('on-user-loggedIn', () => {
      this.submit()
    })
  },
  computed: {
    ...mapState({ visibility: (state) => state.confirmationModal.visibility }),
    ...mapState({ confirmData: (state) => state.confirmationModal.data }),
    ...mapState({ serverErrors: (state) => state.serverErrors }),
    ...mapUserState(['user']),

    term() {
      return this.confirmData.form.term
    },
    longTerm() {
      return this.term == 1
    },
    shortTerm() {
      return this.term == 0
    },
  },
  watch: {
    visibility(newVal) {
      newVal
        ? this.$refs['confirmation-modal'].show()
        : this.$refs['confirmation-modal'].hide()
    },
  },
  beforeDestroy() {
    this.hideConfirmationModal()
  },
  methods: {
    ...mapActions(['hideConfirmationModal', 'sendOffer', 'hideSendOfferModal']),
    onCloseModal() {
      this.hideConfirmationModal()
    },
    submit() {
      var self = this

      let requestBody = {}
      if (this.longTerm) {
        const { move_in_date, move_out_date, ...otherProperties } = JSON.parse(
          JSON.stringify(this.confirmData.form)
        )
        requestBody = otherProperties
      } else {
        requestBody = JSON.parse(JSON.stringify(this.confirmData.form))
      }
      if (this.notes != '') {
        this.$set(requestBody, 'notes', this.notes)
      }
      requestBody.type = 0
      if (!this.user) {
        this.$store.dispatch(
          'AuthModule/showAuthModal',
          {},
          {
            root: true,
          }
        )
        return
      } else {
        this.submitingLoading = true
        this.$axios
          .$post('/requests', requestBody)
          .then(() => {
            this.notes = ''
            window.dataLayer = window.dataLayer || []
            window.dataLayer.push({
              event: 'offer-sent',
              'unit-id': requestBody.property_id,
              price: requestBody.budget,
              duration: requestBody.duration,
            })
          })
          .finally(() => {
            this.$bvModal.show('success-modal')
            setTimeout(() => {
              this.showModel = false
              this.hideSendOfferModal()
              this.$refs['confirmation-modal'].hide()
              this.$bvModal.hide('success-modal')
            }, 2000)
            this.submitingLoading = false
          })
      }
    },
  },
}
</script>
<style lang="scss" scoped>
::v-deep() {
  #confirmation-modal {
    background: $grayTransparentColor;
    &.modal {
      display: flex !important;
      height: 100%;
    }
    .modal-footer {
      align-items: center;
      border: 0px !important;
      padding: 25px;
      justify-content: space-between;
    }
    .modal-header,
    .modal-body {
      @extend .modal-footer;
      padding-bottom: 0;
    }
    .modal-dialog {
      max-width: 551px;
      margin: auto;
    }

    .modal-title {
      font-size: 18px;
      font-weight: 700;
      margin: 0px;
    }
    .close-button {
      background: none;
      border: none;
      margin: 0px;
      padding: 0px;
      line-height: 1;
    }

    .modal-content {
      border-radius: 0;
    }

    .offer-info {
      display: flex;
      flex-direction: row;
      margin-bottom: 8px;
      justify-content: space-between;
      font-size: 12px;
      padding: 0px;
      .offer-info-title {
        color: $secondaryTextColor;
      }
      &.offer-info-total {
        padding-top: 13px;
        padding-bottom: 13px;
        margin-top: 10px;
        border-top: 1px solid $grayLightColor;
        border-bottom: 1px solid $grayLightColor;
        font-weight: bold;
        .offer-info-title {
          color: $mainTextColor;
        }
      }
    }
    .notes-for-landlord {
      margin: 15px 0 0;
      padding: 0px;
      textarea {
        width: 100%;
        min-width: 100%;
        min-height: 70px;
      }
      .modal-actions {
        display: flex;
        justify-content: flex-end;
        margin: 0;
      }
    }
    .sendoffer {
      padding: 8px 14px;
      font-size: 14px;
      font-weight: 400;
      background-color: $mainColorLight;
      border-color: $mainColorLight;
      color: #ffffff;
    }
    svg {
      width: 24px;
      height: 24px;
    }
  }
}
</style>
