<template>
  <div class="property-card">
    <NuxtLink :to="localePath(`/rent/${item.property_slug}?term=${term}`)">
      <div class="card-images">
        <ImageSlider :isHome="isHome" :images="item.images > 3 ? item.images.slice(0, 3) : item.images" />
      </div>
    </NuxtLink>
    <div class="card-content">
      <div class="container">
        <NuxtLink :to="localePath(`/rent/${item.property_slug}?term=${term}`)">
          <div class="card-title">
            <NuxtLink :to="localePath(`/rent/${item.property_slug}?term=${term}`)">
              {{ item.property_name | displayExcerpt(40) }}
            </NuxtLink>
          </div>
          <div class="card-amenities">
            <div class="amenity">
              <svg-icon name="grey-bed" width="16" height="16" fill="#48535b" />
              <span> {{ item.no_of_bedroom }}  {{ dir === 'ltr' ? 'bed' : 'سرير' }} </span>
            </div>
            <div class="amenity">
              <svg-icon name="grey-bath" width="16" height="16" fill="#48535b" />
              <span> {{ item.no_bathrooms }} {{ dir === 'ltr' ? 'bath' : 'حمام' }} </span>
            </div>
            <div :class="['amenity', isHome ? 'd-none' : '']">
              <svg-icon name="grey-area" width="16" height="16" fill="#48535b" />
              <span>{{ item.property_area }} {{ dir === 'ltr' ? 'm' : 'م' }}</span>
              <span class="area-unit">&#178;</span>
            </div>
          </div>
          <div class="card-price" v-if="item.min_daily_price !== '0'">
            <span :class="['price', isHome ? 'isHome' : '']">
              {{
                ` ${item.min_daily_price} - ${
                  item.max_daily_price
                } ${$t('common.egp')}`
              }}
            </span>
            <span class="light-gray font-size-small" v-if="dir === 'ltr'">/ Day</span>
            <span class="light-gray font-size-small" v-else>/ نطاق يومى</span>
          </div>
          <div class="card-price" v-if="item.min_monthly_price !== '0'">
            <span class="price">
              {{
                ` ${item.min_monthly_price} - ${
                  item.min_monthly_price
                } ${$t('common.egp')}`
              }}
            </span>
            <span class="light-gray font-size-small" v-if="dir === 'ltr'">/ Month</span>
            <span class="light-gray font-size-small" v-else>/ نطاق شهرى</span>
          </div>
        </NuxtLink>
        <div class="card-actions" v-if="!isHome">
          <b-button variant="link" @click="updateFavourite(item.property_id)">
            <svg-icon
              :name="item.isFav ? 'Heart-filled' : 'Heart'"
              width="22px"
              height="22px" :class="dir === 'rtl' ? 'ml-2' : 'mr-2'"></svg-icon>
            {{ item.isFav ? dir === 'ltr' ? 'Remove from favourites' : 'حذف من المفضلة' : dir === 'ltr' ? 'Add to favourites' : 'أضف غلى المفضلة' }}
          </b-button>
          <!-- Comment Add to Compare Button -->
          <div class="vertical-line"></div>
          <b-button variant="link" @click="sendProperty(item.property_id)">
            <img
              width="18px"
              height="18px"
              :src="require('@/assets/images/sidebar/Plus.svg')" :class="dir === 'rtl' ? 'ml-2' : 'mr-2'" />
            {{ dir === 'ltr' ? 'Add to compare' : 'أضف للمقارنة' }}
          </b-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ImageSlider from './ImageSlider';
import { createNamespacedHelpers } from 'vuex';
const { mapState } = createNamespacedHelpers('PropertyModule');
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    isHome: {
      type: Boolean,
      default: false,
    }
  },
  components: {
    ImageSlider,
  },
  computed: {
    ...mapState({
      termState: (state) => state.term,
    }),
    term() {
      let cardTerm = this.item.favourite_term === 1 ? 1 : 0;
      let term = this.$route.query.term ? this.termState : cardTerm;
      return term;
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  methods: {
    updateFavourite(property_id) {
      this.$emit('updateFavouriteProperty', {
        property_id,
        term: this.term,
      });
    },
    sendProperty(id) {
      this.$emit('update', { id, term: this.term });
    },
  },
};
</script>

<style lang="scss">
.property-card {
  margin: 0 0 16px;
  background-color: #fff;
  border: 1px solid #e8e8e8;
  border-radius: 16px;

  .card-images {
    div {
      border-radius: 16px 16px 0 0;
    }
  }

  .card-content {
    .container {
      .card-title {
        font-size: 16px;
        font-weight: bold;
        margin: 10px 0 8px;

        a {
          color: #111;
        }
      }

      .card-amenities {
        display: flex;

        .amenity {
          font-size: 13px;
          color: rgba(38, 45, 61, 0.64);

          &:nth-child(2) {
            margin: 0 16px;
          }
        }
      }

      .card-price {
        margin: 16px 0;

        .price {
          color: #0078ff;
          font-weight: bold;
          font-size: 16px;
          &.isHome {
            font-size: 11px;
          }
        }

        .font-size-small {
          font-size: 14px;
        }
      }

      .card-actions {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 16px 0;
        border-top: 1px solid #e8e8e8;

        .btn {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          text-decoration: none;
          font-size: 14px;
          color: #111;
          padding: 8px;

          // img,
          // .icon {
          //   margin-right: 6px;
          // }
        }

        .vertical-line {
          width: 1px;
          height: 36px;
          background-color: #e8e8e8;
        }
      }
    }
  }
}
</style>