<template>
  <b-carousel
    id="carousel-fade"
    class="property-image-slider"
    fade
    indicators
    img-width="1024"
    img-height="480">
    <b-carousel-slide
      :class="isHome ? 'isHome' : ''"
      v-for="(image, index) in images"
      :key="index"
      :img-src="image"></b-carousel-slide>
  </b-carousel>
</template>

<script>
export default {
  name: 'PropertyImageSlider',
  props: {
    images: {
      type: Array,
      required: false,
    },
    isHome: {
      type: Boolean,
      default: false,
    }
  },
};
</script>

<style lang="scss">
#carousel-fade.property-image-slider {
  .carousel-item {
    &.isHome {
      img {
        height: 100px !important;
      }
    }
    img {
      width: 100%;
      height: 220px;
      object-fit: cover;
    }
  }
}
</style>